'use strict';

var querystring = require('querystring');

var request = require('./request.js');

var stringify = function stringify(options) {
  if (typeof options === 'string') {
    return encodeURI(options);
  }

  return querystring.stringify(options);
};

var wrap = function wrap(handler) {
  return function (args) {
    return request(handler(args));
  };
};

module.exports = {
  stringify: wrap(stringify),
  request: wrap(stringify),
  words: wrap(function (options) {
    return "words?".concat(stringify(options));
  }),
  sug: wrap(function (options) {
    return "sug?".concat(stringify(options));
  })
};